<template>
  <div>

    <div class="w3-panel w3-padding w3-margin w3-text-black w3-large w3-center" 
      style="width:200px; float:right;">
      <span>{{ getCurrentUserEmail() }}</span>
    </div>
    <!-- toolbar -->
    <div class="w3-right-align">
      <div class="w3-panel w3-padding w3-margin w3-text-black w3-hover-blue w3-large w3-round-large w3-center" 
        style="cursor:pointer; width:200px; float:right;" @click="signOut">
        <i class="fa fa-user-circle-o" aria-hidden="true" style="font-size:30px;"></i>
        <span>Sign Out</span>
      </div>
      <div class="w3-panel w3-padding w3-margin w3-text-black w3-hover-blue w3-large w3-round-large w3-center" 
        style="cursor:pointer; width:200px; float:right;" @click="gotoEdit">
        <i class="fa fa-area-chart" aria-hidden="true" style="font-size:30px;"></i>
        <span>Edit Image</span>
      </div> 
      <div class="w3-panel w3-padding w3-margin w3-text-black w3-hover-blue w3-large w3-round-large w3-center" 
        style="cursor:pointer; width:200px; float:right;" @click="gotoModels">
        <i class="fa fa-clone" aria-hidden="true" style="font-size:30px;"></i>
        <span>Create Models</span>
      </div>
         
    </div>

    <!-- body -->
    <div class="w3-container w3-cell-row">
      <div class="w3-container w3-cell w3-large">
        <button class="w3-button w3-orange w3-round-large" style="width:100px;" @click="reset">Reset</button> 
        <button class="w3-button w3-green w3-round-large" style="width:100px;"
          onclick="document.getElementById('train-modal').style.display='block'">Train</button> 
      </div>
    </div>

    <div class="w3-container w3-margin">

      <!-- form -->
      <form enctype="multipart/form-data" novalidate>
        <div class="dropbox">
          <input type="file" multiple accept="image/*" class="input-file" 
            @change="filesChange($event.target.files);" >
            <p>
              Drag your images here to upload
            </p>
        </div>
      </form>

      <!-- training successful alert -->
      <div class="w3-panel w3-green w3-display-container w3-round-large" v-if="showSuccess">
        <span 
          @click="closeSuccessAlert"
          class="w3-button w3-green w3-large w3-display-topright w3-round-large">&times;</span>
        <h3>Success!</h3>
        <p>The training is successful. You got a new model: {{ modelName }}</p>
      </div>
    
      <!-- training error alert -->
      <div class="w3-panel w3-red w3-display-container" w3-round-large v-if="showFail">
        <span 
          @click="closeFailAlert"
          class="w3-button w3-red w3-large w3-display-topright w3-round-large">&times;</span>
        <h3>Failed!</h3>
        <p>The training is unsuccessful. Error: {{ errMsg }} </p>
      </div>

      <div class="w3-container w3-margin">
        <img height="250" v-for="base64data in base64Images" :src="base64data">
      </div>
    </div>

    <!-- train dialog-->
    <div id="train-modal" class="w3-modal w3-large">
      <div class="w3-modal-content w3-card-4 w3-animate-zoom w3-padding-large" style="max-width:450px">

        <div class="w3-center"><br>
          <span onclick="document.getElementById('train-modal').style.display='none'" 
            class="w3-button w3-xlarge w3-hover-red w3-display-topright" title="Close Modal">&times;</span>
          <i class="fa fa-cogs" aria-hidden="true" style="color:dodgerBlue; font-size:120px;"></i>  
        </div>

        <div class="w3-container">
          <div class="w3-section">
            <input class="w3-input w3-border w3-padding w3-margin-bottom w3-round" 
              type="text" placeholder="Model name" v-model="modelName" required>
            <button class="w3-button w3-block w3-green w3-section w3-padding w3-round" type="button" @click="train">Start to train</button>
          </div>
        </div>

      </div>
    </div>

    <!-- training icon -->
    <div id="train-icon" class="w3-modal w3-large">
      <div class="animation-wrapper">
        <div class="fa-animation">
          <i class="fa fa-cog fa-lg fa-spin"></i>
          <i class="fa fa-cog fa-md fa-spin"></i>
          <i class="fa fa-cog fa-sm fa-spin"></i>
        </div>
        Training...
      </div>
    </div>

    


  </div>
</template>

<script>
import { auth } from '../firebase';
import { serverURL } from '../config.js'

export default {
  name: 'models',
  created() {
    if (!this.getCurrentUser()) {
      this.$router.push('/');
    }
  },
  data () {
    return {
      modelName: 'a',
      uploadError: null,
      uploadedImages: [],
      base64Images: [],
      showSuccess: false,
      showFail: false,
      errMsg: ''
    }
  },
  methods: {
    gotoModels() {
	    this.$router.push('/models');
    },
    gotoEdit() {
	    this.$router.push('/edit');
    },
    signOut() {
      auth.doSignOut();
	    this.$router.push('/');
    },

    getCurrentUser() {
      return auth.getCurrentUser();
    },
    getCurrentUserEmail() {
      if (auth.getCurrentUser()) {
        return auth.getCurrentUser().email;
      } else {
        return null;
      }
    },

    filesChange(fileList) {
      if (!fileList.length) return;
      
      const fileArr = Object.values(fileList);
      let base64Images = this.base64Images;
      fileArr.map(file => {
        var reader = new FileReader();
        reader.addEventListener("load", function () {
          base64Images.push(this.result);
        }, false);
        reader.readAsDataURL(file);
      });
    },
    reset() {
      this.uploadedImages = [];
      this.base64Images = [];
    },
    getBase64Image(img) {
        const canvas = document.createElement('canvas');
        canvas.width = img.width;
        canvas.height = img.height;

        const ctx = canvas.getContext('2d');
        ctx.drawImage(img, 0, 0);

        const dataURL = canvas.toDataURL('image/png');
        return dataURL;
    },
    closeSuccessAlert(){
      this.showSuccess = false;
    },
    closeFailAlert(){
      this.showFail = false;
    },
    train() {
      if(this.modelName === '') {
        console.log('Please enter Model name...')
        return;
      }
      document.getElementById('train-modal').style.display='none';
      document.getElementById('train-icon').style.display='block';
      
      const thisVue = this;
      // fake...
      setTimeout(function() {
        const data = {
          "userId": 999,
          "title": 'test title',
          "body": 'test body'
        }
        fetch('https://jsonplaceholder.typicode.com/posts', {
          method: "POST",
          mode: "cors",
          body: JSON.stringify(data),
        })
        .then(res => {
          console.log("Train successfully. response = ", res);
          document.getElementById('train-icon').style.display='none';
          thisVue.showSuccess = true;
          thisVue.uploadedImages = [];
          thisVue.base64Images = [];
        })
        .catch(err => {
          console.error("Train failed. err = ", err);
          document.getElementById('train-icon').style.display='none';
          thisVue.errMsg = err.message;
          thisVue.showFail = true;
        });
      }, 2000);

      // call real service todo
      // console.log(serverURL);
      // const data = {
      //   userId: this.getCurrentUserEmail(),
      //   modelName: this.modelName,
      //   trainSets: this.base64Images
      // }
      // fetch(`${serverURL}/model`, {
      //   method: "POST",
      //   mode: "cors",
      //   body: JSON.stringify(data),
      // })
      // .then(res => {
      //   console.log("Train successfully. response = ", res);
      //   document.getElementById('train-icon').style.display='none';
      //   thisVue.showSuccess = true;
      //   thisVue.uploadedImages = [];
      //   thisVue.base64Images = [];
      // })
      // .catch(err => {
      //   console.error("Train failed. err = ", err);
      //   document.getElementById('train-icon').style.display='none';
      //   thisVue.errMsg = err.message;
      //   thisVue.showFail = true;
      // });

    }
  }
}
</script>

<style scoped>
  .dropbox {
    outline: 3px dashed grey;
    outline-offset: -10px;
    background: lightSkyBlue;
    color: dimgray;
    padding: 10px 10px;
    min-height: 140px; /* minimum height */
    position: relative;
    cursor: pointer;
    border-radius: 15px;
  }

  .input-file {
    opacity: 0;
    width: 100%;
    height: 140px;
    position: absolute;
    cursor: pointer;
  }

  .dropbox:hover {
    background: DeepSkyBlue;
    color: black;
  }

  .dropbox p {
    font-size: 2em;
    text-align: center;
    padding: 10px 0;
  }
  .animation-wrapper{
    height:120px; 
    width:120px; 
    padding:20px; 
    border-radius:20px;
    margin:100px auto;
    background-color:gainsboro; 
    color:dimGrey;
    text-align:center;
    font-weight:500;
  }
  
  .fa-animation{
    height:65px; 
    width:65px; 
    margin:0 auto;
    position:relative;
  }
  
  .fa-animation .fa{position:absolute;}
  
  .fa-lg{font-size:45px; top:8px; left:0px; color:orangeRed;}
  .fa-md{font-size:30px; top:0px; left:40px; color: dodgerBlue;}
  .fa-sm{font-size:24px; top:30px; left:35px; color: gold;}
</style>



