<template>
  <div>

    <!-- toolbar -->
    <div class="w3-row" >
      <div class="w3-quarter w3-xlarge">
        <div class="w3-panel w3-padding-8">
          <span></span>
        </div>
      </div>
      <div class="w3-threequarter">
        
      </div>
    </div>

    <!-- body -->
    <div class="w3-display-container w3-blue w3-center" style="height:500px;">
      <div class="w3-container w3-display-middle">
        <div class="w3-xxlarge">Image Enhancement and Editing Robot</div>
        <div class="w3-xxlarge" >
          <font style="color:gold; font-size:40px"><i class="fa fa-cog fa-spin fa-3x fa-fw"></i></font>
          <font style="color:limeGreen; font-size:56px;"><i class="fa fa-cog fa-spin fa-3x fa-fw"></i></font>
        </div>
        <div class="w3-xxlarge" ><font style="color:orangeRed; font-size:32px"><i class="fa fa-cog fa-spin fa-3x fa-fw"></i></font></div>
        <br>
        <div class="w3-large w3-row">
          <div class="w3-half w3-padding">
            <button 
              class="w3-button w3-dark-grey w3-border-dark-grey w3-round-large"
              style="width: 120px"
              onclick="document.getElementById('signin-modal').style.display='block'">Sign in</button>
          </div>
          <div class="w3-half w3-padding">
            <button 
              class="w3-button w3-dark-grey w3-border-dark-grey w3-round-large"
              style="width: 120px"
              onclick="document.getElementById('signup-modal').style.display='block'">Sign up</button>
          </div>
        </div>
      </div>
    </div>

    <div class="w3-display-container w3-align-left w3-padding w3-margin w3-text-grey w3-large">
      <div class="w3-container">Created by:</div>
    </div>
    <div class="w3-row w3-text-grey w3-large">
      <div class="w3-container w3-quarter ">
        <p>
          <ul style="list-style: none;">
            <li><i class="fa fa-user-circle" aria-hidden="true"></i> Chang-Ching Wang</li>
            <li><i class="fa fa-address-card-o" aria-hidden="true"></i> chinchin0107@gmail.com</li>
          </ul>
        </p> 
      </div>
      <div class="w3-container w3-quarter ">
        <p>
          <ul style="list-style: none;">
            <li><i class="fa fa-user-circle" aria-hidden="true"></i> Jiarui Hu</li>
            <li><i class="fa fa-address-card-o" aria-hidden="true"></i> annhjr@gmail.com</li>
          </ul>
        </p> 
      </div>
      <div class="w3-container w3-quarter ">
        <p>
          <ul style="list-style: none;">
            <li><i class="fa fa-user-circle" aria-hidden="true"></i> Kang-Hua Wu</li>
            <li><i class="fa fa-address-card-o" aria-hidden="true"></i> kang-hua.wu@sjsu.edu</li>
          </ul>
        </p> 
      </div>
      <div class="w3-container w3-quarter ">
        <p>
          <ul style="list-style: none;">
            <li><i class="fa fa-user-circle" aria-hidden="true"></i> Qing Huang</li>
            <li><i class="fa fa-address-card-o" aria-hidden="true"></i> qih0008@gmail.com</li>
          </ul>
        </p> 
      </div>
    </div>

    <!-- Sign in Modal-->
    <div id="signin-modal" class="w3-modal w3-large">
      <div class="w3-modal-content w3-card-4 w3-animate-zoom w3-padding-large" style="max-width:450px">

        <div class="w3-center"><br>
          <span onclick="document.getElementById('signin-modal').style.display='none'" 
            class="w3-button w3-xlarge w3-hover-red w3-display-topright" title="Close Modal">&times;</span>
          <i class="fa fa-user-circle-o" aria-hidden="true" style="color:dodgerBlue; font-size:120px;"></i>
        </div>

        <div class="w3-container">
          <div class="w3-section">
            <input class="w3-input w3-border w3-padding w3-margin-bottom w3-round" 
              type="text" placeholder="Email" name="email" required v-model="email">
            <input class="w3-input w3-border w3-padding w3-round" type="password" placeholder="Password" name="password" required v-model="password">
            <button class="w3-button w3-block w3-green w3-section w3-padding w3-round" type="button" @click="signIn">Sign in</button>
          </div>
        </div>

      </div>
    </div>

    <!-- Sign up Modal-->
    <div id="signup-modal" class="w3-modal w3-large">
      <div class="w3-modal-content w3-card-4 w3-animate-zoom w3-padding-large" style="max-width:450px">

        <div class="w3-center"><br>
          <span onclick="document.getElementById('signup-modal').style.display='none'" 
            class="w3-button w3-xlarge w3-hover-red w3-display-topright" title="Close Modal">&times;</span>
          <i class="fa fa-user-circle-o" aria-hidden="true" style="color:dodgerBlue; font-size:120px;"></i>
        </div>

        <div class="w3-container">
          <div class="w3-section">
            <input class="w3-input w3-border w3-padding w3-margin-bottom w3-round" 
              type="text" placeholder="Email" name="email" required v-model="email">
            <input class="w3-input w3-border w3-padding w3-round" type="password" placeholder="Password" name="password" required v-model="password">
            <button class="w3-button w3-block w3-green w3-section w3-padding w3-round" type="button" @click="signUp">Sign up</button>
          </div>
        </div>

      </div>
    </div>

    <!-- msg Modal-->
    <div id="msg-modal" class="w3-modal w3-large">
      <div class="w3-modal-content w3-card-4 w3-animate-zoom w3-padding-large" style="max-width:450px">

        <div class="w3-center"><br>
          <span onclick="document.getElementById('msg-modal').style.display='none'" 
            class="w3-button w3-xlarge w3-hover-red w3-display-topright" title="Close Modal">&times;</span>
        </div>

        <div class="w3-container">
          <div class="w3-section">
            {{ errMsg }}
          </div>
        </div>

      </div>
    </div>


  </div>
</template>

<script>
import { auth } from '../firebase';

export default {
  name: 'home',
  created() {
    if (this.getCurrentUser()) {
      this.$router.push('/models');
    }
  },
  data () {
    return {
      email: '',
      password: '',
      errMsg: ''
    }
  },
  methods: {
    signUp() {
      auth.doCreateUserWithEmailAndPassword(this.email, this.password)
        .then(user => {
          console.log('Create User: ', user.email);
          this.$router.push('/models');
        })
        .catch(error => {
          console.log('Registered User Error: ', error);
          this.errMsg = error.message;
          document.getElementById('msg-modal').style.display='block';
        });
    },
    signIn() {
      auth.doSignInWithEmailAndPassword(this.email, this.password)
        .then(() => {
          console.log('Sign in success...');
          this.$router.push('/models');
        })
        .catch(error => {
          console.log('Sign in Error:', error)
          this.errMsg = error.message;
          document.getElementById('msg-modal').style.display='block';
        });
      
    },
    getCurrentUser() {
      return auth.getCurrentUser();
    },
  }
}
</script>

<style scoped>

</style>



