<template>
  <div>
    <div class="w3-panel w3-padding w3-margin w3-text-black w3-large w3-center" 
      style="width:200px; float:right;">
      <span>{{ getCurrentUserEmail() }}</span>
    </div>
    <!-- toolbar -->
    <div class="w3-right-align">
      <div class="w3-panel w3-padding w3-margin w3-text-black w3-hover-blue w3-large w3-round-large w3-center" 
        style="cursor:pointer; width:200px; float:right;" @click="signOut">
        <i class="fa fa-user-circle-o" aria-hidden="true" style="font-size:30px;"></i>
        <span>Sign Out</span>
      </div>
      
      <div class="w3-panel w3-padding w3-margin w3-text-black w3-hover-blue w3-large w3-round-large w3-center" 
        style="cursor:pointer; width:200px; float:right;" @click="gotoEdit">
        <i class="fa fa-area-chart" aria-hidden="true" style="font-size:30px;"></i>
        <span>Edit Image</span>
      </div>
      <div class="w3-panel w3-padding w3-margin w3-text-black w3-hover-blue w3-large w3-round-large w3-center" 
        style="cursor:pointer; width:200px; float:right;" @click="gotoModels">
        <i class="fa fa-clone" aria-hidden="true" style="font-size:30px;"></i>
        <span>Create Models</span>
      </div>    
    </div>

    <!-- body -->
    
    <div class="w3-row w3-container w3-padding-left w3-xlarge"><p>Upload the image to be edit.</p></div>
    <div class="w3-row w3-container">
      <div class="w3-container w3-third w3-large">
        <form enctype="multipart/form-data" novalidate>
          <div class="dropbox">
            <input type="file" accept="image/*" class="input-file" 
              @change="getImage($event.target.files[0]);" >
              <p>
                Drag your images here
              </p>
          </div>
        </form>
        <button class="w3-button w3-orange w3-round-large w3-block w3-margin-top" @click="reset">Reset</button> 
      </div>
      <div class="w3-container w3-twothird">
        <div>
          <img :src="imageToBeEdit">
        </div>
      </div>
    </div>

    <div class="w3-row w3-container w3-padding-left w3-xlarge"><p>Select a model to use.</p></div>
    <div class="w3-row w3-container">
      <div class="w3-container w3-third">
        <select class="w3-select w3-border" name="option" v-model="modelID">
          <option value="" disabled selected>Choose a Model</option>
          <option v-for="model in models" :value="model.id"> {{ model.name }} </option>
        </select>
      </div>
      <div class="w3-container w3-twothird"></div>
    </div>


    <div class="w3-row w3-container w3-padding-left w3-xlarge"><p>Start to Edit it.</p></div>
    <div class="w3-row w3-container">
      <div class="w3-container w3-third w3-large">
        <button class="w3-button w3-green w3-round-large w3-block" @click="edit">Start</button>
      </div>
      <div class="w3-container w3-twothird"></div>
    </div>
    <div class="w3-container w3-twothird"></div>

    <div class="w3-row w3-container w3-padding-left w3-xlarge"><p>Downlolad the Result Image</p></div>
    
    <div class="w3-row w3-container">
      <div class="w3-container w3-third w3-padding w3-large">
        Click the image to download.
      </div>
      <div class="w3-container w3-twothird">
        <a download="New Image.png" :href="resultImage">
          <img id="resultImage" :src="resultImage">
        </a>
      </div>
    </div>

    <!-- eidt successful alert -->
    <div class="w3-panel w3-green w3-display-container w3-round-large" v-if="showSuccess">
      <span 
        @click="closeSuccessAlert"
        class="w3-button w3-green w3-large w3-display-topright w3-round-large">&times;</span>
      <h3>Success!</h3>
      <p>The Editing is successful.</p>
    </div>
    
    <!-- edit error alert -->
    <div class="w3-panel w3-red w3-display-container" v-if="showFail">
      <span 
        @click="closeFailAlert"
        class="w3-button w3-red w3-large w3-display-topright">&times;</span>
      <h3>Failed!</h3>
      <p>The Editing is unsuccessful. Error: {{ errMsg }} </p>
    </div>
  
    <!-- Message Dialog-->
    <div id="msg-modal" class="w3-modal w3-large">
      <div class="w3-modal-content w3-card-4 w3-animate-zoom w3-padding-large" style="max-width:450px">

        <div class="w3-center"><br>
          <span onclick="document.getElementById('msg-modal').style.display='none'" 
            class="w3-button w3-xlarge w3-hover-red w3-display-topright" title="Close Modal">&times;</span>
        </div>

        <div class="w3-container">
          <div class="w3-section">
            {{ errMsg }}
          </div>
        </div>

      </div>
    </div>

    <!-- working icon -->
    <div id="working-icon" class="w3-modal w3-large">
      <div class="animation-wrapper">
        <div class="fa-animation">
          <i class="fa fa-cog fa-lg fa-spin"></i>
          <i class="fa fa-cog fa-md fa-spin"></i>
          <i class="fa fa-cog fa-sm fa-spin"></i>
        </div>
        Training...
      </div>
    </div>
  
  </div>
</template>

<script>
import { auth } from '../firebase';
import { serverURL } from '../config.js'

export default {
  name: 'dashboard',
  data () {
    return {
      imageToBeEdit: '',
      resultImage: '',
      modelID: '',
      models: [],
      showSuccess: false,
      showFail: false,
      errMsg: ''
    }
  },
  created: function() {
    let thisVue = this;

    //fake
    fetch('https://jsonplaceholder.typicode.com/posts/')
      .then(res => {
        console.log(res);
        const models = [
          {id: 'model001', name: 'Modern Style'},
          {id: 'model002', name: 'Vintage Style'},
          {id: 'model003', name: 'Simple Style'}
        ];
        thisVue.models = models;
      })
      .then(json => console.log(json))

    // real servie todo
    // fetch(`${serverURL}/model`)
    //   .then(function(res) {
    //     return res.json();
    //   })
    //   .then(function(data) {
    //     console.log(data);
    //     thisVue.models = data.models;
    //   })
    //   .catch(err => {
    //     console.error("Query model error. ERROR = ", err);
    //   });
  },
  methods: {
    gotoModels() {
	    this.$router.push('/models');
    },
    gotoEdit() {
	    this.$router.push('/edit');
    },
    signOut() {
      auth.doSignOut();
	    this.$router.push('/');
    },
    getCurrentUser() {
      return auth.getCurrentUser();
    },
    getCurrentUserEmail() {
      if (auth.getCurrentUser()) {
        return auth.getCurrentUser().email;
      } else {
        return null;
      }
    },

    getImage(file) {
      console.log(file)
      let thisVue = this;
      const reader = new FileReader();
      reader.addEventListener("load", function () {
        thisVue.imageToBeEdit = reader.result;
        console.log(thisVue.imageToBeEdit)
      }, false);
      reader.readAsDataURL(file);
    },
    reset() {
      this.imageToBeEdit = '';
      this.resultImage = '';
    },
    closeSuccessAlert(){
      this.showSuccess = false;
    },
    closeFailAlert(){
      this.showFail = false;
    },
    edit() {
      if(this.imageToBeEdit === '') {
        console.log('Please upload a image to be edit...')
        this.errMsg = `Please upload a image to be edit.`;
        document.getElementById('msg-modal').style.display='block';
        return;
      }
      if(this.modelID === '') {
        console.log('Please select a model...')
        this.errMsg = `Please select a model.`;
        document.getElementById('msg-modal').style.display='block';
        return;
      }
      const thisVue = this;
      document.getElementById('working-icon').style.display='block';

      //fake to remove
      thisVue.showSuccess = true;
      thisVue.resultImage = thisVue.imageToBeEdit;
      document.getElementById('working-icon').style.display='none';

      // call real service todo
      // console.log(serverURL);
      // const data = {
      //   image: this.imageToBeEdit,
      //   modelID: this.modelID
      // }
      // fetch(`${serverURL}/edit`, {
      //   method: "POST",
      //   mode: "cors",
      //   body: JSON.stringify(data),
      // })
      // .then(function(res) {
      //   return res.json();
      // })
      // .then(data => {
      //   console.log("Edit successfully. data = ", data);
      //   document.getElementById('working-icon').style.display='none';
      //   thisVue.showSuccess = true;
      //   this.resultImage = data.image;
      // })
      // .catch(err => {
      //   console.error("Edit failed. err = ", err);
      //   document.getElementById('working-icon').style.display='none';
      //   thisVue.errMsg = err.message;
      //   thisVue.showFail = true;
      // });
    },
    download() {
      let thisVue = this;
      let link = document.createElement('a');
      link.textContent = 'download image';
      link.addEventListener('click', function(event) {
          link.href = thisVue.resultImage
          link.download = "mypainting.png";
      }, false);

      document.body.appendChild(link);
    }
  }
}
</script>

<style scoped>
  img {
    width: 600px;
    height: auto;
  }
  .dropbox {
    outline: 3px dashed grey;
    outline-offset: -10px;
    background: lightSkyBlue;
    color: dimgray;
    padding: 10px 10px;
    min-height: 140px; /* minimum height */
    position: relative;
    cursor: pointer;
    border-radius: 15px;
  }

  .input-file {
    opacity: 0;
    width: 100%;
    height: 140px;
    position: absolute;
    cursor: pointer;
  }

  .dropbox:hover {
    background: DeepSkyBlue;
    color: black;
  }

  .dropbox p {
    font-size: 1.5em;
    text-align: center;
    padding: 10px 0;
  }
  .animation-wrapper{
    height:120px; 
    width:120px; 
    padding:20px; 
    border-radius:20px;
    margin:100px auto;
    background-color:gainsboro; 
    color:dimGrey;
    text-align:center;
    font-weight:500;
  }
  
  .fa-animation{
    height:65px; 
    width:65px; 
    margin:0 auto;
    position:relative;
  }
  
  .fa-animation .fa{position:absolute;}
  
  .fa-lg{font-size:45px; top:8px; left:0px; color:orangeRed;}
  .fa-md{font-size:30px; top:0px; left:40px; color: dodgerBlue;}
  .fa-sm{font-size:24px; top:30px; left:35px; color: gold;}
</style>



